/*-------- About --------*/

.about {

    &__wrapper {
        position: relative;
    }

    &__vision {
        display: flex;
        padding-top: 174px;
        position: relative;
    }

    &__vision-title {
        font-family: $font-r;
        font-size: 30px;
        font-weight: 400;
        color: #161616;
    }

    &__vision-text {
        margin-top: 64px;
        font-size: 19px;
        color: #161616;
        line-height: 1.7;
    }

    &__vision-image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 449px;
        
        position: relative;
        overflow: hidden;

        border-radius: 5px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
    }

    &__vision-image-wrapper:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 95%;
    }

    &__vision-image {
        height: 140%;
        position: absolute;
        top: -40%;
    }

    &__vision-count {
        width: 181px;

        padding-top: 40px;
        padding-bottom: 20px;
        padding-left: 21px;
        padding-right: 57px;

        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        background-color: #ffffff;
        border-radius: 5px;

        position: absolute;
        left: -38px;
        bottom: -43px;

        color: #161616;
        font-size: 20px;
        line-height: 1.3;
    }

    &__vision-count strong {
        display: block;
        margin-bottom: 14px;
        font-family: $font-b;
        font-size: 40px;
        line-height: 1;
    }

    &__vision-column--1 {
        padding-top: 10px;
    }

    &__vision-column--2 { 
        flex-shrink: 0;
    }

    &__vision-column--2 > div {
        padding: 10px;
        padding-left: 138px;
        padding-bottom: 53px;
        position: relative;
    }

    &__title {
        font-family: $font-pf;
        font-size: 64px;
        text-align: center;
    }

    &__title--values {
        margin-top: 159px;   
    }

    &__values {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-top: 80px;
        padding-bottom: 143px;
    }

    &__value {
        width: calc(25% - 14px);
        padding: 31px 24px 49px 24px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        background-color: #ffffff;
        border-radius: 10px;
    }

    &__value-icon-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 97px;
        height: 97px;

        border-radius: 50%;
        background-color: #f3f3f3;
    }

    &__value-icon {
        width: 100%;
    }

    &__value-title {
        margin-top: 42px;
        font-family: $font-sb;
        font-size: 27px;
    }

    &__value-text {
        margin-top: 35px;
        font-size: 19px;
        line-height: 1.6;
    }

    &__expertise {
        padding-top: 140px;
        padding-bottom: 164px;
        position: relative;
    }

    &__expertise-list {
        display: flex;
        justify-content: space-between;
        margin-top: 100px;
        font-family: $font-sb;

        z-index: 10;
        position: relative;
    }

    &__expertise-item:not(:last-child) {
        padding-right: 30px;
    }

    &__expertise-title {
        display: flex;
        align-items: center;
        font-size: 27px;
    }

    &__expertise-icon {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 97px;
        height: 97px;
        margin-right: 30px;
        border-radius: 50%;
        background-color: #f3f3f3;
    }

    &__expertise-icon img {
        width: 75%;
    }

    &__expertise-text {
        margin-top: 48px;
        padding-left: 17px;
    }

    &__expertise-text-wrapper {
        display: flex;
        align-items: center;
        font-size: 19px;
        color: #161616;
        line-height: 1.3;
    }

    &__expertise-text-wrapper:before {
        content: '';
        flex-shrink: 0;
        width: 6px;
        height: 6px;
        margin-right: 18px;

        border-radius: 50%;
        background-color: #2a9df4;
    }

    &__expertise-text-item:not(:first-child) {
        margin-top: 22px;
    }

    &__title--nav {
        color: #ffffff;
        font-family: $font-sb;
        font-size: 35px;
        text-align: center;
    }

    &__expertise-nav {
        width: calc(100% + 40px);
        margin-left: -20px;
        padding: 20px 10px;
        padding-bottom: 30px;
        position: -webkit-sticky;
        position: sticky;
        top: 90px;
        z-index: 11;
        background-color: #1d4693;
    }

    &__expertise-navlist {
        display: flex;
        justify-content: center;
        margin-top: 30px;
    }

    &__expertise-navitem:not(:first-child) {
        margin-left: 30px;
    }

    &__expertise-navlink {
        padding-bottom: 7px;
        font-family: $font-sb;
        font-size: 16px;
        color: #8aa0ca;
        border-bottom: 2px solid hsla(0,0%,83.5%,.36470588235294116);
        text-align: center;
    }

    &__expertise-navlink.active {
        color: #fff;
        border-color: #fff;
    }

    &__title--small {
        font-family: $font-sb;
        font-size: 27px;
    }

    &__clients {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 75px;
    }

    &__client {
        display: flex;
        align-items: center;
        justify-content: center;
        
        width: calc(25% - 10px);

        position: relative;
    }

    &__client:nth-child(n+5) {
        margin-top: 15px;
    }

    &__client:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 100%;
    }

    &__client-background {
        width: 100%;
        height: 100%;
        background-color: #f9f9f9;

        position: absolute;
    }

    &__client-logo--main {
        width: 73%;
        position: absolute;
        z-index: 2;
    }

    &__client-logo--small {
        width: 55%;
    }

    &__client-hidden {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        
        width: 100%;
        height: 100%;
        padding: 40px;

        position: absolute;
        z-index: 2;

        background-color: #ffffff;

        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 0);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 0);

        -webkit-transition: -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        -o-transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
        transition: clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1), -webkit-clip-path 0.56s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &__client-link {
        margin-top: 32px;
        font-family: $font-sb;
        font-size: 19px;
        color: #8f8f8f;
        letter-spacing: 0.76px;
    }

    &__client:hover &__client-hidden {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    &__approach {
        position: relative;
        padding-top: 253px;
        padding-bottom: 139px;
    }

    &__approach-wrapper {
        position: relative;
        z-index: 1;
    }

    &__approach-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 155px;
    }

    &__approach-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: calc(33% - 40px);
        text-align: center;
    }

    &__approach-item:nth-child(n+4) {
        margin-top: 80px;
    }

    &__approach-item-icon {
        width: 90px;
        height: 90px;
    }

    &__approach-item-title {
        margin-top: 25px;
        font-size: 19px;
        color: #1d4693;
    }

    &__approach-item-text {
        margin-top: 35px;
        font-size: 19px;
        line-height: 1.6;
    }
}

@media (max-width: 1219px) {

    .about {

        &__vision {
            padding-top: 100px;
        }

        &__vision-column--2 > div {
            padding-left: 100px;
        }

        &__expertise {
            padding-top: 100px;
        }

        &__expertise-list {
            margin-top: 50px;
        }

        &__expertise-icon {
            width: 70px;
            height: 70px;
            margin-right: 20px;
        }

        &__expertise-title {
            font-size: 22px;
        }

        &__expertise-text {
            margin-top: 30px;
        }

        &__approach {
            padding-top: 100px;
            padding-bottom: 100px;
        }

        &__approach-list {
            margin-top: 60px;
        }
    }
}

@media (max-width: 1099px) {

    .about {

        &__vision-image-wrapper {
            width: 370px;
        }

        &__vision-image {
            height: 136%;
            top: -36%;
        }

        &__vision-count {
            width: 130px;
            padding: 15px;
            left: -30px;
            bottom: -30px;
            font-size: 16px;
        }

        &__vision-count strong {
            margin-bottom: 10px;
            font-size: 33px;
        }

        &__vision-title {
            font-size: 23px;
        }

        &__vision-text {
            margin-top: 30px;
            font-size: 18px;
            line-height: 1.6;
        }

        &__vision-column--2 > div {
            padding-left: 80px;
        }

        &__title--values {
            margin-top: 100px;
        }

        &__values {
            padding-top: 50px;
            padding-bottom: 100px;
        }

        &__value {
            padding: 30px 20px 30px 20px;
        }

        &__value-icon-wrapper {
            width: 85px;
            height: 85px;
        }

        &__value-title {
            margin-top: 20px;
            font-size: 22px;
        }

        &__value-text {
            margin-top: 20px;
            font-size: 16px;
        }

        &__approach-item {
            width: calc(33% - 14px);
        }
    }
}

@media (max-width: 989px) {

    .about {

        &__vision {
            flex-direction: column;
            padding-top: 50px;
        }

        &__vision-column--1 {
            padding-top: 0;
        }

        &__vision-column--2 {
            display: flex;
            justify-content: center;
        }

        &__vision-column--2 > div {
            padding-left: 10px;
            padding-right: 10px;
            padding-bottom: 10px;
            padding-top: 30px;
        }

        &__vision-column--2 > div, &__vision-image-wrapper {
            width: 100%;
        }

        &__vision-count {
            left: -1px;
            bottom: -1px;
            box-shadow: none;
        }

        &__vision-image {
            height: 137%;
            top: -37%;
        }

        &__value {
            width: calc(50% - 10px);
        }

        &__value:nth-child(n+3) {
            margin-top: 20px;
        }

        &__expertise {
            padding-top: 0;
            padding-bottom: 60px;
        }

        &__expertise-list {
            flex-direction: column;
        }

        &__expertise-item:not(:last-child) {
            padding-right: 0;
        }

        &__expertise-item:not(:first-child) {
            margin-top: 70px;
        }

        &__expertise-text {
            display: flex;
            flex-wrap: wrap;
        }

        &__expertise-text-item {
            display: flex;
            align-items: center;
            width: calc(33% - 10px);
        }

        &__expertise-text-item:not(:first-child) {
            margin-top: 0;
        }

        &__expertise-text-item:not(:nth-child(3n+1)) {
            margin-left: 18px;
        }

        &__expertise-text-item:nth-child(n+4) {
            margin-top: 20px;
        }

        &__expertise-text-wrapper {
            font-size: 15px;
        }

        &__clients {
            margin-top: 40px;
        }

        &__approach-item-title {
            margin-top: 20px;
            font-size: 18px;
        }

        &__approach-item-text {
            margin-top: 20px;
            font-size: 16px;
        }

        &__approach-item:nth-child(n+4) {
            margin-top: 40px;
        }

        &__client-link {
            margin-top: 20px;
            font-size: 15px
        }

        &__client-hidden {
            padding: 0;
        }
    }
}

@media (max-width: 767px) {

    .about {

        &__expertise-text-item {
            width: calc(50% - 10px);
        }

        &__expertise-text-item:nth-child(n) {
            margin-left: 0;
            margin-top: 0;
        }

        &__expertise-text-item:nth-child(n+3) {
            margin-top: 20px;
        }

        &__expertise-text-item:not(:nth-child(2n+1)) {
            margin-left: 20px;
        }

        &__clients {
            justify-content: flex-start;
        }

        &__client {
            width: calc(33% - 10px);
        }

        &__client:nth-child(n+5) {
            margin-top: 0;
        }

        &__client:nth-child(n+4) {
            margin-top: 20px;
        }

        &__client:not(:nth-child(3n+1)) {
            margin-left: 17px;
        }

        &__approach {
            padding-top: 80px;
            padding-bottom: 20px;
        }

        &__approach-list {
            margin-top: 30px;
        }

        &__approach-item {
            width: calc(33% - 10px);
        }

        &__approach-item-title {
            font-size: 15px;
        }

        &__approach-item-text {
            font-size: 14px;
        }

        &__client-hidden {
            padding: 20px;
        }

        &__client-link {
            font-size: 16px;
        }
    }
}

@media (max-width: 619px) {

    .about {

        &__vision-title {
            font-size: 21px;
        }

        &__vision-text {
            font-size: 16px;
        }

        &__clients {
            justify-content: space-between;
        }

        &__client {
            width: calc(50% - 8px);
        }

        &__client:nth-child(n) {
            margin-left: 0;
            margin-top: 0;
        }

        &__client:nth-child(n+3) {
            margin-top: 18px;
        }

        &__approach-item {
            width: calc(50% - 8px);
        }

        &__approach-item:nth-child(n) {
            margin-top: 0;
        }

        &__approach-item:nth-child(n+3) {
            margin-top: 20px;
        }

        &__title--values {
            font-size: 52px;
        }
    }
}

@media (max-width: 479px) {

    .about {

        &__values {
            padding-top: 30px;
            padding-bottom: 80px;
        }

        &__value {
            width: 100%;
        }

        &__value-wrapper {
            display: flex;
            align-items: center;
        }

        &__value-title {
            margin-top: 0;
            margin-left: 10px;
        }

        &__value-icon-wrapper {
            width: 60px;
            height: 60px;
        }

        &__value:nth-child(n) {
            margin-top: 20px;
        }

        &__value:not(:first-child) {
            margin-top: 20px;
        }

        &__expertise-nav {
            top: 70px;
        }

        &__title--nav {
            font-size: 23px;
        }

        &__expertise-navlist {
            margin-top: 15px;
        }

        &__expertise-navlink {
            padding-bottom: 2px;
            font-size: 3.5vw;
        }

        &__expertise-navitem:not(:first-child) {
            margin-left: 10px;
        }

        &__expertise-list {
            margin-top: 30px;
        }

        &__expertise-text {
            padding-left: 0;
        }

        &__expertise-text-item {
            width: calc(50% - 5px);
        }

        &__expertise-text-wrapper {
            font-size: 4vw;
        }

        &__expertise-text-wrapper:before {
            margin-right: 10px;
        }

        &__expertise-text-item:not(:nth-child(2n+1)) {
            margin-left: 10px;
        }

        &__client {
            width: calc(50% - 5px);
        }

        &__client:nth-child(n+3) {
            margin-top: 10px;
        }

        &__client-link {
            margin-top: 10px;
            font-size: 3.8vw;
        }
    }
}

@media (max-width: 359px) {

    .about {

        &__expertise-nav {
            width: calc(100% + 20px);
            margin-left: -10px;
        }
    }
}